export default {
	data() {
		return {
			list: [],
			parentsWidth: '',
			active: 0
		}
	},
	methods: {
		set(val) {
			console.log(val)
			this.list = val;
			this.list.forEach((item, index) => {
				if (item.cid == getApp().globalData.cid) {
					this.active = index
				}
			})
			console.log(this.active)
			this.parentsWidth = val.length * 290 + 'rpx'
		},
		detail(e, k) {
			this.active = k;
			this.$emit('click', e)
		}
	}
}
